import { FC, ReactNode } from 'react';

import { Box, TypographyProps } from '@mui/material';
import { StyledRootProps } from '@packages/types';
import { Typography } from 'components/Typography';
import AddressView, { AddressViewProps } from 'views/AddressView';
import AmenitiesStackView, { AmenitiesStackViewProps } from 'views/AmenitiesStackView';
import { MissingAmenitiesStackView, MissingAmenitiesStackViewProps } from 'views/ListingItem';
import PriceView, { PriceViewProps } from 'views/PriceView';
import RatingView, { RatingViewProps } from 'views/RatingView';

export interface ListingInfoBlockProps
  extends Pick<RatingViewProps, 'reviewsCount'>,
    Pick<AddressViewProps, 'address' | 'zip' | 'withTooltip'>,
    Pick<PriceViewProps, 'price'>,
    StyledRootProps {
  amenitiesItems: AmenitiesStackViewProps['items'];
  onClick?: () => void;
  pricePrefix?: PriceViewProps['prefix'];
  isFullPrice?: boolean;
  amenitiesTypographyProps?: TypographyProps;
  ratingTypographyProps?: TypographyProps;
  divider?: ReactNode;
  rating?: RatingViewProps['rating'];
  name: string;
  missingAmenities?: MissingAmenitiesStackViewProps['items'];
  isPartial?: boolean;
}

export const ListingInfoBlock: FC<ListingInfoBlockProps> = (props) => {
  const {
    onClick,
    address,
    zip,
    name,
    price,
    amenitiesItems,
    pricePrefix,
    rating,
    reviewsCount,
    amenitiesTypographyProps,
    ratingTypographyProps,
    divider,
    withTooltip,
    missingAmenities,
    isPartial = false,
  } = props;

  return (
    <Box width="100%" py={1.5} onClick={onClick}>
      <Box display="flex" justifyContent="space-between" alignItems="center" gap={2}>
        <Typography
          withTooltip={withTooltip}
          variant="h6"
          sx={{ textOverflow: 'ellipsis', overflow: 'hidden', whiteSpace: 'nowrap' }}
        >
          {name}
        </Typography>
        {!!rating && (
          <RatingView
            rating={rating}
            reviewsCount={reviewsCount}
            iconSize="small"
            typographyProps={{ variant: 'h6', ...ratingTypographyProps }}
            withoutReviewCountText
          />
        )}
      </Box>
      <AddressView
        variant="subtitle1"
        sx={{
          overflow: 'hidden',
          textOverflow: 'ellipsis',
          whiteSpace: 'nowrap',
          mt: 0.5,
        }}
        withTooltip={withTooltip}
        address={address}
        zip={zip}
      />
      <AmenitiesStackView
        items={amenitiesItems}
        divider={divider}
        typographyProps={{ variant: 'subtitle1', ...amenitiesTypographyProps }}
        sx={{ mt: 0.5 }}
      />
      <PriceView
        price={price}
        variant="per_night"
        prefix={pricePrefix}
        typographyProps={{ variant: 'h6' }}
        sx={{ mt: 0.5 }}
      />

      {isPartial && !!missingAmenities?.length && (
        <Box
          mt={0.5}
          position="relative"
          sx={{ cursor: 'initial' }}
          onClick={(event) => {
            // stop propagation onClick
            event.preventDefault();
          }}
        >
          <MissingAmenitiesStackView items={missingAmenities} />
        </Box>
      )}
    </Box>
  );
};
