import { FC, SyntheticEvent } from 'react';

import { Divider, Stack, Tooltip, tooltipClasses, Typography } from '@mui/material';
import { useBoolean } from '@packages/helpers';
import { IconButton } from 'buttons/IconButton';
import { MoreAmenitiesView } from 'views/ListingItem';

import { MissingAmenityIcon } from './MissingAmenityIcon';

export interface MissingAmenitiesStackViewProps {
  items: Array<{
    iconName: string;
    title: string;
  }>;
}

export const AMENITIES_LIMIT = 3;

export const MissingAmenitiesStackView: FC<MissingAmenitiesStackViewProps> = (props) => {
  const { items } = props;

  const { setTrue: openTooltip, setFalse: closeTooltip, value: isTooltipOpen } = useBoolean(false);

  const handleOpen = (event?: SyntheticEvent<Element>) => {
    // // stop propagation onClick
    event?.preventDefault();
    openTooltip();
  };

  const handleClose = (event?: SyntheticEvent<Element, Event>) => {
    // // stop propagation onClick
    event?.preventDefault();
    closeTooltip();
  };

  const missingItemsCount = items.length - AMENITIES_LIMIT;

  return (
    <Stack
      direction="row"
      divider={
        <Divider
          sx={{ height: 28, borderWidth: 1, borderColor: 'other.grey' }}
          orientation="vertical"
        />
      }
      spacing={2}
      alignItems="center"
      position="relative"
    >
      {items.slice(0, AMENITIES_LIMIT).map((item) => {
        const { iconName, title } = item;

        return <MissingAmenityIcon key={iconName} icon={iconName} title={title} />;
      })}
      {missingItemsCount > 0 && (
        <Tooltip
          open={isTooltipOpen}
          placement="top-start"
          disableHoverListener
          title={<MoreAmenitiesView items={items.slice(AMENITIES_LIMIT)} onClose={handleClose} />}
          PopperProps={{
            sx: {
              [`& .${tooltipClasses.tooltip}`]: {
                // horizontal padding 8px on mobile
                maxWidth: { xs: 'calc(100% - 16px)', sm: 400 },
                ml: { xs: 1, sm: 0 },
              },
            },
          }}
        >
          <IconButton onClick={handleOpen}>
            <Typography
              variant="button"
              color="text.secondary"
              sx={{ fontSize: 16, textDecoration: 'underline' }}
            >
              +{missingItemsCount}
            </Typography>
          </IconButton>
        </Tooltip>
      )}
    </Stack>
  );
};
