import { FC, MouseEvent } from 'react';

import { Box } from '@mui/material';
import { IconButton } from 'buttons/IconButton';
import { Typography } from 'components/Typography';
import { IcoMoon } from 'custom/IcoMoon';

import { AmenityChip } from './AmenityChip';
import { MissingAmenitiesStackViewProps } from './MissingAmenitiesStackView';

export interface MoreAmenitiesViewProps {
  items: MissingAmenitiesStackViewProps['items'];
  onClose: (event: MouseEvent<HTMLButtonElement>) => void;
}

export const MoreAmenitiesView: FC<MoreAmenitiesViewProps> = (props) => {
  const { items, onClose } = props;

  return (
    <Box
      onClick={(event) => {
        // stop propagation onClick
        event.preventDefault();
      }}
    >
      <Box justifyContent="space-between" display="flex" alignItems="center" mb={2}>
        <Typography variant="subtitle2">pages.search.labels.missing_amenities</Typography>
        <IconButton onClick={onClose} aria-label="close" size="small" sx={{ ml: 1 }}>
          <IcoMoon icon="close" />
        </IconButton>
      </Box>
      <Box sx={{ maxHeight: 150, overflowY: 'auto', display: 'flex', flexWrap: 'wrap', gap: 1 }}>
        {items.map((item, index) => (
          <AmenityChip key={index} iconName={item.iconName} title={item.title} />
        ))}
      </Box>
    </Box>
  );
};
