import { FC, ReactNode, useCallback, useState } from 'react';

import { Box, IconButton, IconButtonProps } from '@mui/material';
import { useBoolean } from '@packages/helpers';
import { clsx } from 'clsx';
import { IcoMoon } from 'custom/IcoMoon';
import SwiperCore from 'swiper';
import { Navigation, Pagination } from 'swiper/modules';
import { Swiper, SwiperProps, SwiperSlide } from 'swiper/react';
import { styled } from 'theme/index';

import 'swiper/css/pagination';

export type Swiper = typeof SwiperCore;

const PREFIX = 'ListingItemSlider';

export const ListingItemSliderClasses = {
  navigationButtonWrapper: `${PREFIX}-navigationButtonWrapper`,
  disabledNavigationButtonWrapper: `${PREFIX}-disabledNavigationButtonWrapper`,
  prevButtonWrapper: `${PREFIX}-prevButtonWrapper`,
  nextButtonWrapper: `${PREFIX}-nextButtonWrapper`,
  navigationButton: `${PREFIX}-navigationButton`,
  prevButton: `${PREFIX}-prevButton`,
  nextButton: `${PREFIX}-nextButton`,

  // swiper core and modules classes, add others if need
  paginationBullet: 'swiper-pagination-bullet',
  paginationActiveBullet: 'swiper-pagination-bullet-active',
  paginationContainer: 'swiper-pagination',
  paginationFraction: 'swiper-pagination-fraction',
};

export const StyledRoot = styled(Box, {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
  shouldForwardProp: (p) => {
    return p !== 'shouldNavigationButtonsWrapperIgnorePointerEvents';
  },
})<{ shouldNavigationButtonsWrapperIgnorePointerEvents: boolean }>(
  ({ theme, shouldNavigationButtonsWrapperIgnorePointerEvents }) => {
    const isRtl = theme.direction === 'rtl';

    return {
      position: 'relative',

      '&:hover': {
        cursor: 'pointer',
        [`& .${ListingItemSliderClasses.navigationButtonWrapper}`]: {
          opacity: 1,
        },
      },

      [`& .${ListingItemSliderClasses.navigationButtonWrapper}`]: {
        position: 'absolute',
        top: '50%',
        transform: 'translateY(-50%)',
        zIndex: 2,
        transition: '0.3s',
        [theme.breakpoints.up('md')]: {
          opacity: 0,
        },
      },
      [`& .${ListingItemSliderClasses.disabledNavigationButtonWrapper}`]: {
        pointerEvents: shouldNavigationButtonsWrapperIgnorePointerEvents ? 'none' : 'auto',
        [`& .${ListingItemSliderClasses.navigationButton}`]: {
          visibility: 'hidden',
        },
      },
      [`& .${ListingItemSliderClasses.prevButtonWrapper}`]: {
        left: theme.spacing(1),
      },
      [`& .${ListingItemSliderClasses.nextButtonWrapper}`]: {
        right: theme.spacing(1),
      },
      [`& .${ListingItemSliderClasses.navigationButton}`]: {
        [theme.breakpoints.up('md')]: {
          opacity: 0.8,
        },
        backgroundColor: theme.palette.primary.contrastText,
        '& svg': {
          color: theme.palette.text.light,
        },
        ':hover': {
          opacity: 1,
          backgroundColor: theme.palette.primary.contrastText,
        },
      },

      [`& .${ListingItemSliderClasses.prevButton}`]: {
        transform: isRtl ? 'rotate(180deg)' : 'rotate(0deg)',
      },

      [`& .${ListingItemSliderClasses.nextButton}`]: {
        transform: isRtl ? 'rotate(0deg)' : 'rotate(180deg)',
      },

      [`& .${ListingItemSliderClasses.paginationContainer}`]: {
        lineHeight: 0,
        bottom: 8,
      },
      [`& .${ListingItemSliderClasses.paginationBullet}`]: {
        width: 6,
        height: 6,
        opacity: 0.6,
        backgroundColor: theme.palette.primary.contrastText,
      },
      [`& .${ListingItemSliderClasses.paginationFraction}`]: {
        bottom: 24,
      },
      [`& .${ListingItemSliderClasses.paginationActiveBullet}`]: {
        opacity: 1,
      },
    };
  },
);

type ListingItemSliderProps = {
  items: Array<ReactNode>;
};

const ArrowButton = (props: Omit<IconButtonProps, 'children'>) => {
  const { className, ...rest } = props;

  return (
    <IconButton size="small" className={clsx(ListingItemSliderClasses.navigationButton)} {...rest}>
      <IcoMoon icon="chevron-left" size="small" className={className} />
    </IconButton>
  );
};

export const ListingItemSlider: FC<ListingItemSliderProps> = (props) => {
  const { items } = props;

  const {
    value: shouldNavigationButtonsWrapperIgnorePointerEvents,
    setTrue: setNavigationButtonsWrapperIgnorePointerEvents,
    setFalse: setNavigationButtonsWrapperEmitPointerEvents,
  } = useBoolean(false);

  const [isFirstGroup, setIsFirstGroup] = useState(false);
  const [isLastGroup, setIsLastGroup] = useState(false);

  const [prevElement, setPrevElement] = useState<HTMLDivElement | null>();
  const [nextElement, setNextElement] = useState<HTMLDivElement | null>();

  const [activeIndex, setActiveIndex] = useState<number>(0);

  const setGroupsStatus = useCallback((swiper: SwiperCore) => {
    const { isBeginning, isEnd } = swiper;

    setIsFirstGroup(isBeginning);
    setIsLastGroup(isEnd);
  }, []);

  const handleResize = useCallback<NonNullable<SwiperProps['onResize']>>(
    (swiper) => {
      setGroupsStatus(swiper);
    },
    [setGroupsStatus],
  );

  /**
   * We need to assign custom navigation elements before swiper init
   * because they don't work when we just pass it to navigation options
   */
  const handleBeforeInit = useCallback<NonNullable<SwiperProps['onBeforeInit']>>(
    (swiper) => {
      const navigation = swiper.params.navigation;

      if (navigation && typeof navigation !== 'boolean') {
        navigation.prevEl = prevElement;
        navigation.nextEl = nextElement;
      }
    },
    [nextElement, prevElement],
  );

  const handleAfterInit = useCallback<NonNullable<SwiperProps['onAfterInit']>>(
    (swiper) => {
      setGroupsStatus(swiper);
    },
    [setGroupsStatus],
  );

  const handleSlideChange = useCallback<NonNullable<SwiperProps['onSlideChange']>>(
    (swiper) => {
      const { isBeginning, isEnd } = swiper;

      setActiveIndex(swiper.activeIndex);
      setGroupsStatus(swiper);

      if (isBeginning || isEnd) {
        /**
         * We don't want user to be redirected to the listing page
         * when he clicks the place where navigation button was before
         * disapearing. However, we enable this when he leaves the mouse
         * from the listing and after that clicks on this place.
         */
        setNavigationButtonsWrapperEmitPointerEvents();
      }
    },
    [setGroupsStatus, setNavigationButtonsWrapperEmitPointerEvents],
  );

  return (
    <StyledRoot
      onMouseLeave={setNavigationButtonsWrapperIgnorePointerEvents}
      shouldNavigationButtonsWrapperIgnorePointerEvents={
        shouldNavigationButtonsWrapperIgnorePointerEvents
      }
    >
      {items.length > 1 && (
        <Box
          ref={setPrevElement}
          className={clsx(
            ListingItemSliderClasses.navigationButtonWrapper,
            ListingItemSliderClasses.prevButtonWrapper,
          )}
        >
          <ArrowButton className={ListingItemSliderClasses.prevButton} disabled={isFirstGroup} />
        </Box>
      )}

      <Swiper
        modules={[Navigation, Pagination]}
        allowTouchMove={false}
        navigation={
          items.length
            ? {
                prevEl: prevElement,
                nextEl: nextElement,
                disabledClass: ListingItemSliderClasses.disabledNavigationButtonWrapper,
              }
            : false
        }
        pagination={{
          clickable: true,
          enabled: items.length > 1,
          dynamicBullets: true,
          dynamicMainBullets: 3,
        }}
        onBeforeInit={handleBeforeInit}
        onAfterInit={handleAfterInit}
        onResize={handleResize}
        onSlideChange={handleSlideChange}
      >
        {items.map((item, index) => {
          const shouldRenderChild = Math.abs(index - activeIndex) <= 1; // render only sibling images to active image (not all images)

          return <SwiperSlide key={index}>{shouldRenderChild ? item : null}</SwiperSlide>;
        })}
      </Swiper>

      {items.length > 1 && (
        <Box
          ref={setNextElement}
          className={clsx(
            ListingItemSliderClasses.navigationButtonWrapper,
            ListingItemSliderClasses.nextButtonWrapper,
          )}
        >
          <ArrowButton className={ListingItemSliderClasses.nextButton} disabled={isLastGroup} />
        </Box>
      )}
    </StyledRoot>
  );
};

export default ListingItemSlider;
