import { FC, forwardRef } from 'react';

import { ButtonProps } from '@mui/material';
import { clsx } from 'clsx';

import { IconButtonClasses, StyledButton, StyledIconButton } from './StyledIconButton';
import { IconButtonProps } from './types';

/**
 * IconButton
 * @param props.variant - controls buttons styles. This component expects icon as children with any variant.
 */
export const IconButton: FC<IconButtonProps> = forwardRef((props: IconButtonProps, ref) => {
  const { children, variant, color, size = 'medium', className, ...rest } = props;

  const buttonClassnames = clsx(IconButtonClasses.button, {
    [IconButtonClasses.large]: size === 'large',
    [IconButtonClasses.medium]: size === 'medium',
    [IconButtonClasses.small]: size === 'small',
  });

  const commonProps: ButtonProps = {
    className: clsx(className, buttonClassnames),
    color,
    size,
    ref,
    children,
    ...rest,
  };

  return variant === 'icon' ? (
    <StyledIconButton {...commonProps} />
  ) : (
    <StyledButton {...commonProps} variant={variant} {...rest} />
  );
});

IconButton.displayName = 'IconButton';

export default IconButton;
