import { FC } from 'react';

import { Box } from '@mui/material';
import { StyledRootProps } from '@packages/types';
import NextLink from 'components/links/NextLink';
import { ListingImage, ListingImageProps } from 'views/ListingImage';
import { ListingInfoBlock, ListingInfoBlockProps } from 'views/ListingInfoBlock';

import { UrlObject } from 'url';

import { MissingAmenitiesStackViewProps, PartialChip } from './components';

import { AddressViewProps } from '../AddressView';
import { AmenitiesStackViewProps } from '../AmenitiesStackView';
import { PriceViewProps } from '../PriceView';

export interface ListingItemProps
  extends Pick<ListingInfoBlockProps, 'name' | 'rating' | 'reviewsCount' | 'isPartial'>,
    Pick<AddressViewProps, 'address' | 'zip' | 'withTooltip'>,
    Pick<PriceViewProps, 'price'>,
    Pick<ListingImageProps, 'size' | 'items'>,
    StyledRootProps {
  amenitiesItems: AmenitiesStackViewProps['items'];
  onClick?: () => void;
  pricePrefix?: PriceViewProps['prefix'];
  href?: string | UrlObject;
  missingAmenities?: MissingAmenitiesStackViewProps['items'];
}

