import { FC } from 'react';

import { Box, CardMedia, generateUtilityClasses } from '@mui/material';
import { getUrlWithTemplate, GetUrlWithTemplateParams } from '@packages/helpers';
import { Identifier, StyledRootProps } from '@packages/types';
import { LISTING_ITEM_IMAGE_WIDTH } from 'application/constants';
import { clsx } from 'clsx';
import { styled } from 'theme';
import { NoImageView } from 'views/NoImageView';

import { ListingItemSlider } from './swiper';

const PREFIX = 'ListingImage';

export const ListingImageClasses = generateUtilityClasses(PREFIX, ['root', 'image']);

const StyledRoot = styled(Box, {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(({ theme }) => {
  return {
    [`&.${ListingImageClasses.root}`]: {
      overflow: 'hidden',
      borderRadius: theme.spacing(2),
      width: '100%',
    },

    [`& .${ListingImageClasses.image}`]: {
      width: '100%',
    },
  };
});

interface ImageItem extends GetUrlWithTemplateParams {
  id: Identifier;
  title: string;
}

export interface ListingImageProps extends StyledRootProps {
  items?: ImageItem[];
  size?: 'medium' | 'large';
}

export const ListingImage: FC<ListingImageProps> = (props) => {
  const { items = [], size = 'medium', className, sx } = props;

  const imageHeights: Record<typeof size, number> = {
    medium: 240,
    large: 296,
  };

  const renderImages = items.map(({ id, title, ...rest }) => (
    <CardMedia
      key={id}
      sx={{ height: imageHeights[size] }}
      className={ListingImageClasses.image}
      image={getUrlWithTemplate({
        width: LISTING_ITEM_IMAGE_WIDTH,
        ...rest,
      })}
    />
  ));

  return (
    <StyledRoot className={clsx(ListingImageClasses.root, className)} sx={sx}>
      {renderImages.length ? (
        <ListingItemSlider items={renderImages} />
      ) : (
        <NoImageView sx={{ height: imageHeights[size] }} />
      )}
    </StyledRoot>
  );
};
