import { FC } from 'react';

import { toCurrencyFormat } from '@packages/helpers';
import { StyledRootProps } from '@packages/types';
import { useCurrencyContext } from 'contexts/CurrencyContext';
import { useTranslation } from 'next-i18next';

import { Typography, TypographyProps } from '../../Typography';

export interface PriceViewProps extends StyledRootProps {
  price: number;
  variant?: 'per_night' | 'default';
  prefix?: 'from' | false | null;
  typographyProps?: Partial<TypographyProps>;
}

export const PriceView: FC<PriceViewProps> = (props) => {
  const { price, prefix, variant = 'default', className, sx, typographyProps } = props;
  const { t } = useTranslation();
  const { currency } = useCurrencyContext();

  const formattedPrice = toCurrencyFormat({
    value: price,
    decimal: 0,
    currencySign: currency?.sign,
  });

  const isFromPrefix = prefix === 'from';
  const isPerNight = variant === 'per_night';

  const priceWithoutPrefix = isPerNight
    ? t('custom.price_per_night', { price: formattedPrice })
    : formattedPrice;

  return (
    <Typography variant="subtitle2" className={className} sx={sx} {...typographyProps}>
      {isFromPrefix
        ? t('custom.from_price', {
            price: priceWithoutPrefix,
            interpolation: { escapeValue: false },
          })
        : priceWithoutPrefix}
    </Typography>
  );
};

export default PriceView;
