import { ComponentType, ReactElement, useCallback, useEffect, useState } from 'react';

import { Box, Button, ButtonProps } from '@mui/material';
import { useTranslate } from 'hooks/index';

interface SlotProps {
  toggleButton?: Partial<ButtonProps>;
}

type Slots = {
  [Key in keyof SlotProps]?: ComponentType<NonNullable<SlotProps[Key]>> | null;
};

export interface ShowMoreWrapperProps<Item = unknown> {
  maxItems: number;
  items: Array<Item>;
  children: (params: {
    items: Array<Item>;
    expanded: boolean;
    isItemsMoreThanMax: boolean;
  }) => ReactElement;
  slots?: Slots;
  slotProps?: SlotProps;
  // in case the component is controlled from the outside
  expanded?: boolean;
  onChange?: (expanded: boolean) => void;
}

export const ShowMoreWrapper = <Item,>(props: ShowMoreWrapperProps<Item>): ReactElement => {
  const { items, maxItems, children, expanded = false, onChange, slots, slotProps } = props;

  const { toggleButton: ToggleButton = Button } = slots || {};
  const { toggleButton: toggleButtonProps } = slotProps || {};

  const {
    sx: toggleButtonSx,
    children: toggleButtonChildren,
    ...restToggleButtonProps
  } = toggleButtonProps || {};

  const translate = useTranslate({ basePath: 'buttons' });

  const [isExpanded, setIsExpanded] = useState(expanded);

  const truncatedItems = items.slice(0, maxItems);
  const isItemsMoreThanMax = items.length > maxItems;

  useEffect(() => {
    setIsExpanded(expanded);
  }, [expanded]);

  const handleToggleClick = useCallback(() => {
    const newValue = isExpanded ? false : true;

    setIsExpanded(newValue);
    onChange?.(newValue);
  }, [isExpanded, onChange]);

  const defaultLabel = isExpanded ? translate('show_less') : translate('show_more');

  return (
    <Box>
      {children({
        items: isExpanded ? items : truncatedItems,
        expanded: isExpanded,
        isItemsMoreThanMax,
      })}
      {isItemsMoreThanMax && ToggleButton && (
        <ToggleButton
          variant="text"
          sx={{ display: 'block', mx: 'auto', mt: 3, ...toggleButtonSx }}
          onClick={handleToggleClick}
          color="secondary"
          {...restToggleButtonProps}
        >
          {toggleButtonChildren ?? defaultLabel}
        </ToggleButton>
      )}
    </Box>
  );
};

export default ShowMoreWrapper;
