import { Button, generateUtilityClasses, IconButton } from '@mui/material';
import { styled } from 'theme/index';

const PREFIX = 'IconButton';

export const IconButtonClasses = generateUtilityClasses(PREFIX, [
  'button',
  'large',
  'medium',
  'small',
]);

const getStyles = () => {
  const largeSize = 48;
  const mediumSize = 40;
  const smallSize = 28;

  return {
    [`&.${IconButtonClasses.button}`]: {
      padding: 0,
    },
    [`&.${IconButtonClasses.large}`]: {
      width: largeSize,
      minWidth: largeSize,
      maxWidth: largeSize,
      height: largeSize,
    },
    [`&.${IconButtonClasses.medium}`]: {
      width: mediumSize,
      minWidth: mediumSize,
      maxWidth: mediumSize,
      height: mediumSize,
    },
    [`&.${IconButtonClasses.small}`]: {
      width: smallSize,
      minWidth: smallSize,
      maxWidth: smallSize,
      height: smallSize,
      minHeight: smallSize,
      maxHeight: smallSize,
    },
  };
};
export const StyledButton = styled(Button, {
  name: `Styled${PREFIX}`,
  label: `Theme--${PREFIX}`,
})(() => getStyles());

export const StyledIconButton = styled(IconButton, {
  name: `Styled${PREFIX}`,
  label: `Theme--${PREFIX}`,
})(() => getStyles());
